@use 'base/variables'as v;
@use 'base/mixins' as m;

.filtros{

    background-color: lighten(v.$grisClaro, 20%);
    margin-top: 8rem;
    border-radius: 1rem;
    padding: 2rem;
    .filtros-inputs{

        @include m.tablet{
            display: flex;
            align-items: center;
            gap: 3rem;
        }
    }

    h2{
        color: v.$azul;
        font-size: 2.7rem;
        @include m.tablet{
          margin: 0;
        }
    }
    
    .campo{
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        text-align: center;
        gap: 1rem;

        @include m.tablet{
            margin-bottom: 0;
        }
    }

    input[type="radio"]{
        accent-color: blue ;
        width: 1.5rem;
        height: 1.5rem;
    }
}