@use 'base/variables' as v;
@use 'base/mixins' as m;

.login{
    margin-top: 2rem;
    padding-bottom: 4rem;
    .agenda{
        @include m.degradar(v.$cyan);
    }

    input[type="submit"]{
        @include m.boton(v.$cyan, v.$blanco);
       
    }

    .contenedor-sm{
       
        @include m.gota(rgba(214, 225, 228, 0.651));
         .boton{
             border-radius:40px;
          
             }      
    
             @include m.tablet{
                height: auto;
                width: auto;
            }


    }

    .acciones{
        a{
               @include m.links(v.$cyan, rgba(195, 230, 239, 0.651))
            
        }
       
        
    }


    .tagline{
        margin: auto;
    }

}



