@use 'base/variables'as v;
@use 'base/mixins' as m;

.dashboard{
    background-color: v.$grisClaro;

    @include m.tablet{
       display: flex;
       height: 100vh;
    }

    .principal{
        flex: 1;
        overflow: scroll;
    }

    .contenido{
       padding: 1rem;
      
        .nombre-pagina{
          font-size: 2.8rem;
          text-align: center;
        }
        .contenedor-sm{
       
            @include m.gota(rgba(214, 225, 228, 0.651));
            input[type="submit"]{
                 border-radius:40px;
                 width: 18rem;
                 background-color: v.$indigo;
                 padding: 1.5rem 4rem;
                 color: v.$blanco;
                 font-size: 1.6rem;
                 font-weight: 700;
                 border: none;
                 transition-property: background-color;
                 transition-duration: 0.80s;
                 text-align: center;
              
                 }      
                      
        }

        @include m.telefono{
            padding: 5rem;
        }

    }

  
}

.contenedor-s{
       
    @include m.gotaMobile(rgba(214, 225, 228, 0.651));
   
      display: block;
  
         @include m.tablet{
            height: auto;
            width: auto;
        }
       
        input[type="submit"]{
            @include m.boton(v.$indigo,v.$blanco );
            width: 18rem;
            border-radius: 5rem;
            
         }
      
         .campo1{
           
            display: grid;
            gap: 3rem;
            margin-bottom: 2rem;
            margin-top: 2rem;
            width: 100%;
    
            @include m.telefono{
                
                width: 100%;
            }
    
            
            input{
                flex: 1;
                padding: 1rem;
                margin:auto;
                border: none;
                background-color: lighten(v.$grisClaro,40%);
                box-shadow: inset 10px 10px 10px rgba(0,0,0,0.08),
                15px 20px 20px rgba(0,0,0,0.07);
                border-radius: 40px;
                width:100%;
                
                @include m.telefono{
                   width: 80%;
                }
         
            }
    
          
         }


         .enlace{
            @include m.links(v.$azul, rgba(191, 219, 237, 0.651));
          
             &:hover{
                cursor: pointer;
             
            } 
         }
}

