@use 'base/variables'as v;
@use 'base/mixins' as m;

.acciones{
   margin-top: 5rem;

   @include m.desktop{
    display: flex;
    justify-content: space-between;
   }

   a{
    display: block;
    text-align: center;
    margin-bottom: 3rem;
    color: v.$negro;
    &:last-of-type{
        margin-bottom: 0;
    }
   }
    
}