@use 'base/variables'as v;
@use 'base/mixins' as m;


.modal{
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
 

    .formulario{
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem); //posicion inicial
        transition-property: all;
        transition-duration: 600ms;
        opacity: 0;
        border-radius: 1rem;
        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-50%); //posicion final
            opacity: 1;
        }

        input[type="text"]{
            border: 1px solid v.$gris;
        }

        .opciones{
            display: flex;
            justify-content: space-between;
        }
        .submit-nueva-tarea{

            @include m.boton(v.$indigo, v.$blanco);
            
        }

        .cerrar-modal{

            @include m.boton(v.$naranja, v.$blanco);
        }

        legend{
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }

        &.cerrar{
            transform: translateY(-200%); //posicion final
            opacity: 0;
        }

        .calendar{
           width: auto;
           max-width: 600px;
           margin: 1em auto;
           padding: .5em 0;

           @include m.telefono{
            width: 80%;
            
          }

           &_info{
            display: flex;
            align-items: center;
            padding: 0 2em;
            font-size: 1.2em;
            text-transform: uppercase;
           }

           &_prev, &_next{
            color: v.$indigo;
            cursor: pointer;
            font-size: 1.4em;
           }

           &_prev{
             margin-right: auto;
           }

           &_next{
             margin-left: auto;
           }

           &_week, &_dates{
            display: grid;
            grid-template-columns: repeat(7,1fr);
            
           }

           &_month, &_year{
            padding: .5em 1em;
           } 

           &_item{
            text-align: center;
            line-height: 2;
            
           }

           &_today{
            background: lightcoral;
            color: v.$blanco;
            width: 50%;
            border-radius: 50%;
            margin: auto;
           }

           &_last-days{
            opacity: .3;
           }

           &_selects{
            background: blue;
            color: v.$blanco;
            width: 50%;
            border-radius: 50%;
            margin: auto;
           
           }

           &_edit{
            background: blue;
            color: v.$blanco;
            width: 50%;
            border-radius: 50%;
            margin: auto;
           
           }

           &_no{
            background: v.$blanco;
            color: v.$negro;
            width: 50%;
            border-radius: 50%;
            margin: auto;
           }
           &:hover{
            cursor: pointer;
           }
          
        }
    }
}