@use 'base/variables'as v;
@use 'base/mixins' as m;


.contenedor-nueva-tarea{
  
    display: flex;
    justify-content: center;
    .agregar-tarea{
           margin-top: 1rem;
        @include m.boton(v.$cyan,v.$blanco);
    }
}

.listado-tareas{
    list-style-type:none;
    padding: 0;
    margin-top: 3rem;
   
    .no-tareas{
        text-align: center;
        
    }
}



.tarea{
   padding: .5rem 2rem;
    border-bottom: 2px solid darken(v.$grisClaro, 5%);
    margin-bottom: 1rem;
    text-align:center;

    @include m.telefono{
        text-align:justify;
        padding: .5rem 2rem;
        border-bottom: 2px solid darken(v.$grisClaro, 5%);
        margin-bottom: 1rem;
        margin-left: 7rem;
        display: block;
    }
   


    &:last-of-type{
        border: none;
  
    }


    p{
        font-size: 1.4rem;
       margin-top: 2rem;
        &:hover{
            cursor: pointer;
        }

        &:last-of-type{
            margin-top: 1.5rem;
            margin-bottom: 1.5rem;
        }
    }

    .opciones{
        
        display: flex;
        width: 2rem;
        gap: 1rem;
     
        @include m.telefono{
            display: flex;
            width: 2rem;
            gap: 1rem;
            margin-left: 40rem;
        }

        button{
            border: none;
            font-size: .9rem;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: 600;
            border-radius: 1rem;
           // height:fit-content;
            &:hover{
                cursor: pointer;
            }
        }

        .completa{
            background-color: v.$azul;
            color: v.$blanco;
        }

        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
        }

        .eliminar-tarea{
            background-color: v.$rojo;
            color: v.$blanco;
        }
    }
}