@use 'base/variables' as v;
@use 'base/mixins' as m;

.reestablecer{

    margin-top: 2rem;
    padding-bottom: 4rem;
    .agenda{
        @include m.degradar(v.$azul);
    }

    .contenedor-sm{
       
        @include m.gota(rgba(191, 232, 237, 0.651));
         .boton{
             border-radius:40px;
             align-items: center;
             
             }      
    
             @include m.tablet{
                height: auto;
                width: auto;
            }
    }

    input[type="submit"]{
        @include m.boton(v.$azul, v.$blanco);
    }

    .acciones{
        a{
           
            @include m.links(v.$azul, rgba(191, 219, 237, 0.651));
        }
    }
}