@use 'base/variables' as v;
@use 'base/mixins' as m;

.olvida{
    margin-top: 2rem;
    padding-bottom: 4rem;

    .agenda{
        @include m.degradar(v.$rojo);
    }

    input[type="submit"]{
        @include m.boton(v.$rojo, v.$blanco);
    }

    .contenedor-sm{
       
        @include m.gota(rgba(240, 203, 203, 0.651));
         .boton{
             border-radius:40px;
       
             }      
    
             @include m.tablet{
                height: auto;
                width: auto;
                align-items: center;
            }
    }


    .acciones{
        a{
           
            @include m.links(v.$rojo, rgba(237, 191, 191, 0.651));
        }

      
    }
}