@use 'base/variables' as v;
@use 'base/mixins' as m;



.modal-container{

        opacity: 0;
        visibility: hidden; 
        position: fixed;
         display: flex;
        justify-content: center;
        align-items: center; 
        z-index:100;
        overflow: auto;
        top:0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 145%;
        height:73rem; 
        background: rgba(0, 0, 0, 0.452); 

  @include m.telefono{
    width: 100%;
    height:100vh;
  }
   
}

.modals{
    width: 28%;
    height: 37.5%;
    background: v.$grisClaro;
   // overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    position: relative;
    transition: transform 1s;
    transform: translateY(0%);
    border-radius: 30px;
  
   
    h2{
     display: flex;
     justify-content: center;
 
    }

    @include m.telefono{
        width: 30%;
        height: 30%;

        h2{
            display: flex;
            justify-content: center;
        }
      }
}

.modal-close{
    transform: translateY(-200%);
}

