@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 0.1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
     
    .campo{
        display: flex;
       // align-items: center;
        margin-bottom: 2rem;
        width: 100%;

        @include m.tablet{
            
            width: 100%;
        }

        label{
            flex: 0 0 10rem;
           
         }
    
        input{
            flex: 1;
            padding: 1rem;
            border: none;
            background-color: lighten(v.$grisClaro,40%);
            box-shadow: inset 10px 10px 10px rgba(0,0,0,0.08),
            15px 20px 20px rgba(0,0,0,0.07);
            border-radius: 40px;
           
     
        }
        &:last-of-type{
            width: 92%;
      
        }
        &:first-of-type{
            width: 92%;
      
        }
    }

   
}

