@use 'base/variables' as v;
@use 'base/mixins' as m;

.crear{

     padding-bottom: 2rem;

    .agenda{
         margin-bottom: 0.1rem;
         margin-top: 0.1rem;
        @include m.degradar(v.$verde);
   
    }

    input[type="submit"]{
        @include m.boton(v.$verde, v.$blanco);
    }

    .contenedor-sm{
       margin-top: 0.8rem;
        @include m.gota(rgba(202, 240, 208, 0.651));
         .boton{
             border-radius:40px;
             align-items: center;
             
             }      
    
             @include m.tablet{
                height: auto;
                width: auto;
            }
    }

    .acciones{
       margin-top: 0.9rem;
        a{
           
            @include m.links(v.$verde, rgba(191, 237, 199, 0.651));
        }

       
    }

    .tagline{
        margin: auto;
    }

    .descripcion-pagina{
        margin-top: unset;
    }
}

