html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.5rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.formulario {
  margin-top: 0.1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.formulario .campo {
  display: flex;
  margin-bottom: 2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario .campo {
    width: 100%;
  }
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: white;
  box-shadow: inset 10px 10px 10px rgba(0, 0, 0, 0.08), 15px 20px 20px rgba(0, 0, 0, 0.07);
  border-radius: 40px;
}
.formulario .campo:last-of-type {
  width: 92%;
}
.formulario .campo:first-of-type {
  width: 92%;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 3rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem;
  font-size: 1.2rem;
  max-width: 50rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.login {
  margin-top: 2rem;
  padding-bottom: 4rem;
}
.login .agenda {
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 6rem;
}
@media (min-width: 768px) {
  .login .agenda {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.8s;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  width: 30rem;
  cursor: pointer;
}
.login .contenedor-sm {
  position: relative;
  width: 100%;
  display: block;
  padding: 23px;
  border-radius: 39% 61% 55% 45%/28% 31% 69% 72%;
  /* border: 1px solid ; */
  box-shadow: 15px 20px 20px rgba(0, 0, 0, 0.07), 15px 25px 10px rgba(0, 0, 0, 0.05), inset 10px 10px 10px rgba(0, 0, 0, 0.08), inset -10px -10px 15px rgba(214, 225, 228, 0.651);
}
.login .contenedor-sm .boton {
  border-radius: 40px;
}
@media (min-width: 768px) {
  .login .contenedor-sm {
    height: auto;
    width: auto;
  }
}
.login .acciones a {
  color: #0891B2;
  border-radius: 5% 95% 40% 60%/40% 0% 100% 60%;
  box-shadow: 15px 20px 20px rgba(0, 0, 0, 0.07), 15px 25px 10px rgba(0, 0, 0, 0.05), inset 10px 10px 10px rgba(0, 0, 0, 0.08), inset -10px -10px 15px rgba(195, 230, 239, 0.651);
  height: 39px;
  width: auto;
  transition: 0.25s;
  padding-top: 0.5rem;
  margin-top: 0.1rem;
}
@media (min-width: 768px) {
  .login .acciones a {
    width: auto;
  }
}
.login .acciones a:hover {
  border-radius: 40% 34% 20% 26%/22% 22% 26% 26%;
}
.login .tagline {
  margin: auto;
}

