.modal-container {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  overflow: auto;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 145%;
  height: 73rem;
  background: rgba(0, 0, 0, 0.452);
}
@media (min-width: 480px) {
  .modal-container {
    width: 100%;
    height: 100vh;
  }
}

.modals {
  width: 28%;
  height: 37.5%;
  background: #f3f4f6;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: relative;
  transition: transform 1s;
  transform: translateY(0%);
  border-radius: 30px;
}
.modals h2 {
  display: flex;
  justify-content: center;
}
@media (min-width: 480px) {
  .modals {
    width: 30%;
    height: 30%;
  }
  .modals h2 {
    display: flex;
    justify-content: center;
  }
}

.modal-close {
  transform: translateY(-200%);
}

