@use 'base/variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
 
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: 0.80s;
    text-align: center;
    width: 100%;
    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
       width: 30rem;
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin degradar($color){
    background: linear-gradient(to right, $color 0%, lighten($color,30%) 100%);
        color: transparent;
        background-clip: text;  //para dar el color al texto
        text-align: center;
        font-size: 6rem;

        @include tablet{
            font-size: 10rem;
        }
}

@mixin links($bgColor, $colorTexto) {
    color: $bgColor;
    border-radius: 5% 95% 40% 60% / 40% 0% 100% 60% ;
    box-shadow: 15px 20px 20px rgba(0,0,0,0.07),
    15px 25px 10px rgba(0,0,0,0.05),
    inset 10px 10px 10px rgba(0,0,0,0.08),
    inset -10px -10px 15px $colorTexto;
    height: 39px;
    width: auto;
    transition: 0.25s;
    padding-top: 0.5rem;
    margin-top: 0.1rem;
    @include tablet {
        width: auto;
    }
    &:hover {
        border-radius: 40% 34% 20% 26% / 22% 22% 26% 26%  ;
    }
}


@mixin gota($bgColor){
    position: relative;
    width: 100%;
   
    display: block;
    padding: 23px;
  
    border-radius: 39% 61% 55% 45% / 28% 31% 69% 72% ;
    /* border: 1px solid ; */
    box-shadow: 15px 20px 20px rgba(0,0,0,0.07),
    15px 25px 10px rgba(0,0,0,0.05),
    inset 10px 10px 10px rgba(0,0,0,0.08),
    inset -10px -10px 15px $bgColor;
}

@mixin gotaMobile($bgColor){
    width: 100%;
    padding: 23px;
    border-radius: 39% 61% 55% 45% / 28% 31% 69% 72% ;
    box-shadow: 15px 20px 20px rgba(0,0,0,0.07),
    15px 25px 10px rgba(0,0,0,0.05),
